//   
export const AIR_DENSITY = 1.2; // /
export const SPECIFIC_HEAT = 1.005; // /(C)
export const GAS_CALORIFIC_VALUE = 8000; // /
export const RECUPERATOR_EFFICIENCY = 0.7; //  
export const NIGHT_TARIFF_DISCOUNT = 0.5; //    
export const WATER_SPECIFIC_HEAT = 4.19; // /(C)
export const WATER_DENSITY = 1000; // /

//   
export const calculatePower = (airFlow, regionTemp, outletTemp, hasRecuperator = false) => {
  // airFlow  /, regionTemp  outletTemp  C
  // AIR_DENSITY  /, SPECIFIC_HEAT  /(C)
  // Q = L *  * c * (Tout - Tavg) / 3600 ()
  //   ,  
  const L = airFlow; // /
  const rho = AIR_DENSITY; // /
  const c = SPECIFIC_HEAT; // /(C)
  const deltaT = outletTemp - regionTemp; // C
  let Q = (L * rho * c * deltaT) / 3600; // 
  if (hasRecuperator) {
    Q = Q * (1 - RECUPERATOR_EFFICIENCY);
  }
  return Q > 0 ? Q.toFixed(2) : 0;
};

//   
export const calculateWaterFlow = (power, supplyTemp, returnTemp) => {
  // power  , supplyTemp  returnTemp  C
  // G = Q / (c *  * t) * 3600 (/)
  const Q = power; // 
  const c = WATER_SPECIFIC_HEAT; // /(C)
  const rho = WATER_DENSITY; // /
  const deltaT = supplyTemp - returnTemp; // C
  if (deltaT <= 0) return 0;
  const G = (Q * 3600) / (c * rho * deltaT); // /
  return G > 0 ? G.toFixed(2) : 0;
};

//   
export const calculateElectricityConsumption = (power, hoursPerDay, daysPerMonth, isNightTariff = false) => {
  // power  , hoursPerDay  /, daysPerMonth  /
  // E = P * t * d (/)
  const P = power; // 
  const t = hoursPerDay; // /
  const d = daysPerMonth; // /
  const E = P * t * d; // /
  return E > 0 ? E.toFixed(2) : 0;
};

//   
export const calculateGasConsumption = (power, hoursPerDay, daysPerMonth) => {
  // power  , hoursPerDay  /, daysPerMonth  /
  // V = (Q * t * d) / ( * H) (/)
  const Q = power; // 
  const t = hoursPerDay; // /
  const d = daysPerMonth; // /
  const eta = 0.9; //  
  const H = GAS_CALORIFIC_VALUE / 860; // / (  /m)
  const V = (Q * t * d) / (eta * H); // /
  return V > 0 ? V.toFixed(2) : 0;
};

//   
export const calculateOperatingCost = (consumption, pricePerKWh, isNightTariff = false) => {
  // consumption  /, pricePerKWh  /
  // C = E * p (/)
  const E = consumption; // /
  const p = isNightTariff ? pricePerKWh * NIGHT_TARIFF_DISCOUNT : pricePerKWh; // /
  const C = E * p; // /
  return C > 0 ? C.toFixed(2) : 0;
};

//   
export const calculateGasCost = (gasConsumption, gasPrice) => {
  // gasConsumption  /, gasPrice  /1000 
  // C = V * p (/)
  const V = gasConsumption; // /
  const p = gasPrice / 1000; // /
  const C = V * p; // /
  return C > 0 ? C.toFixed(2) : 0;
}; 