import React, { useState } from 'react';
import './FlowCalculator.css';

const FLUIDS = [
  { value: 'water', label: '', coef: 1.163 },
  { value: 'pglycol30', label: ' 30%', coef: 1.07 },
  { value: 'pglycol40', label: ' 40%', coef: 1.03 },
  { value: 'pglycol50', label: ' 50%', coef: 0.99 },
  { value: 'eglycol30', label: ' 30%', coef: 1.08 },
  { value: 'eglycol40', label: ' 40%', coef: 1.04 },
  { value: 'eglycol50', label: ' 50%', coef: 1.00 },
  { value: 'glycerin40', label: ' 40%', coef: 1.10 },
  { value: 'glycerin50', label: ' 50%', coef: 1.06 },
];

const FLOW_UNITS = {
  m3h: {
    name: '/',
    toM3h: (val) => val,
    fromM3h: (val) => val,
    min: 0.1,
    max: 10,
    step: 0.01,
    default: 1,
  },
  lmin: {
    name: '/',
    toM3h: (val) => val * 0.06,
    fromM3h: (val) => val / 0.06,
    min: 1,
    max: 200,
    step: 0.1,
    default: 60,
  },
  lsec: {
    name: '/',
    toM3h: (val) => val * 3.6,
    fromM3h: (val) => val / 3.6,
    min: 0.2,
    max: 20,
    step: 0.01,
    default: 1,
  },
};

function formatNumber(num) {
  return Number(num).toLocaleString('ru-RU', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });
}

const FlowCalculator = ({ onBack }) => {
  const [mode, setMode] = useState('flow');
  const [fluid, setFluid] = useState('water');
  const [power, setPower] = useState(15);
  const [flow, setFlow] = useState(1);
  const [flowUnit, setFlowUnit] = useState('m3h');
  const [deltaT, setDeltaT] = useState(20);
  const [tooltip, setTooltip] = useState(false);

  const coef = FLUIDS.find(f => f.value === fluid).coef;
  const unitObj = FLOW_UNITS[flowUnit];

  // 
  const getFormula = () => mode === 'flow' ? 'G = Q / (C  t)' : 'Q = G  C  t';

  // 
  let result = {};
  if (mode === 'flow') {
    const m3h = power / (deltaT * coef);
    result = {
      m3h: isFinite(m3h) ? formatNumber(m3h) : '-',
      lmin: isFinite(m3h) ? formatNumber(m3h * 1000 / 60) : '-',
      lsec: isFinite(m3h) ? formatNumber(m3h * 1000 / 3600) : '-',
    };
  } else {
    const m3h = unitObj.toM3h(flow);
    const q = m3h * coef * deltaT;
    result = {
      power: isFinite(q) ? formatNumber(q) : '-',
    };
  }

  //   
  const handleFlowUnitChange = (e) => {
    const newUnit = e.target.value;
    const m3hVal = FLOW_UNITS[flowUnit].toM3h(flow);
    const newVal = FLOW_UNITS[newUnit].fromM3h(m3hVal);
    setFlowUnit(newUnit);
    setFlow(Number(newVal.toFixed(3)));
  };

  return (
    <>
    <div className="container">
      <div className="card" style={{position: 'relative'}}>
        {onBack && (
          <button
            className="back-btn"
            onClick={onBack}
            aria-label=""
            style={{
              position: 'absolute',
              top: 12,
              left: 12,
              width: 40,
              height: 40,
              borderRadius: 10,
              background: 'var(--main-blue)',
              color: '#fff',
              border: 'none',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
              cursor: 'pointer',
              zIndex: 20,
              transition: 'background 0.2s, transform 0.2s',
              padding: 0,
            }}
            onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
            onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
            onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
          >
            <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15 4L8 11L15 18" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        )}
        <button
          className="info-btn info-btn-square"
          tabIndex={0}
          aria-label=""
          style={{
            position: 'absolute',
            top: 12,
            right: 12,
            width: 40,
            height: 40,
            borderRadius: 10,
            background: 'var(--main-blue)',
            color: '#fff',
            border: 'none',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
            cursor: 'pointer',
            zIndex: 20,
            transition: 'background 0.2s, transform 0.2s',
            padding: 0,
          }}
          onClick={() => setTooltip(!tooltip)}
          onBlur={() => setTooltip(false)}
          onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
          onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
          onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
        >
          <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="11" cy="11" r="10" stroke="white" strokeWidth="2" fill="none"/>
            <rect x="10" y="7" width="2" height="2" rx="1" fill="white"/>
            <rect x="10" y="11" width="2" height="6" rx="1" fill="white"/>
          </svg>
        </button>
        <div className="header-row">
          <div className="card-title">  </div>
        </div>
        <div className="results-block">
          {mode === 'flow' ? (
            <>
              <div className="result-row">
                <span className="result-label">/</span>
                <span className="result-value">{result.m3h}</span>
              </div>
              <div className="result-row">
                <span className="result-label">/</span>
                <span className="result-value">{result.lmin}</span>
              </div>
              <div className="result-row">
                <span className="result-label">/</span>
                <span className="result-value">{result.lsec}</span>
              </div>
            </>
          ) : (
            <div className="result-row">
              <span className="result-label">Q, </span>
              <span className="result-value">{result.power}</span>
            </div>
          )}
        </div>
        <div className="toggle-row">
          <div className="toggle-btn">
            <button
              className={`toggle-option${mode === 'flow' ? ' active' : ''}`}
              onClick={() => setMode('flow')}
            >
                
            </button>
            <button
              className={`toggle-option${mode === 'power' ? ' active' : ''}`}
              onClick={() => setMode('power')}
            >
                
            </button>
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="fluidType"> </label>
          <select
            id="fluidType"
            value={fluid}
            onChange={e => setFluid(e.target.value)}
          >
            {FLUIDS.map(f => (
              <option key={f.value} value={f.value}>{f.label}</option>
            ))}
          </select>
        </div>
        {mode === 'flow' ? (
          <div className="form-group">
            <label htmlFor="power"> ()</label>
            <input
              type="number"
              id="power"
              min={1}
              max={50}
              step={1}
              value={power}
              onChange={e => setPower(Number(e.target.value))}
            />
            <div className="slider-block">
              <input
                type="range"
                min={1}
                max={50}
                step={1}
                value={power}
                onChange={e => setPower(Number(e.target.value))}
              />
            </div>
          </div>
        ) : (
          <div className="form-group">
            <div className="units-row">
              <label htmlFor="flow" style={{marginBottom:0}}>  G</label>
              <select
                id="flowUnit"
                value={flowUnit}
                onChange={handleFlowUnitChange}
                style={{width:'auto',maxWidth:120}}
              >
                  {Object.entries(FLOW_UNITS).map(([key, unit]) => (
                    <option key={key} value={key}>{unit.name}</option>
                ))}
              </select>
            </div>
            <input
              type="number"
                id="flow"
                min={unitObj.min}
                max={unitObj.max}
                step={unitObj.step}
                value={flow}
                onChange={e => setFlow(Number(e.target.value))}
              />
              <div className="slider-block">
                <input
                  type="range"
                  min={unitObj.min}
                  max={unitObj.max}
                  step={unitObj.step}
                  value={flow}
                  onChange={e => setFlow(Number(e.target.value))}
                />
            </div>
          </div>
        )}
        <div className="form-group">
            <label htmlFor="deltaT">  t (C)</label>
          <input
            type="number"
            id="deltaT"
              min={1}
              max={50}
            step={1}
            value={deltaT}
            onChange={e => setDeltaT(Number(e.target.value))}
          />
          <div className="slider-block">
            <input
              type="range"
                min={1}
                max={50}
              step={1}
              value={deltaT}
              onChange={e => setDeltaT(Number(e.target.value))}
            />
          </div>
        </div>
        <div className={`tooltip${tooltip ? ' active' : ''}`} style={{
          position: 'fixed',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          maxWidth: '90vw',
          width: '400px',
          zIndex: 1000
        }}>
          <strong> :</strong>
          <span className="formula">{getFormula()}</span>
          <b>G</b>   , /<br />
          <b>Q</b>   , <br />
          <b>t</b>   , C<br />
          <b>C</b>   ,     (. )<br />
          <br />
          <b>:</b>
          <ul style={{margin:0, paddingLeft:20}}>
            <li>  C = 1.163 /(C)</li>
            <li>    C  </li>
            <li>        </li>
          </ul>
        </div>
        <div className="footer-signature">Galf Design</div>
      </div>
    </div>
      <div className="footer-signature" style={{
        marginTop: 32,
        background: '#f6f7fa',
        borderRadius: 14,
        padding: '22px 16px 20px 16px',
        color: '#232837',
        fontSize: '1.08em',
        lineHeight: 1.85,
        boxShadow: '0 2px 12px 0 rgba(76,201,240,0.07)',
        border: '1.5px solid #e0e4ea',
        maxWidth: 600,
        marginLeft: 'auto',
        marginRight: 'auto',
        position: 'relative',
        textAlign: 'left',
        letterSpacing: 0.01
      }}>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>   ?</span><br/>
                (,   ),     , ,       .    /, /, /.  ,         .          .       .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>  </span><br/>
               :<br/>
          <span style={{display:'inline-block',color:'#232837',background:'#e6f3fa',padding:'7px 15px',borderRadius:7,fontFamily:'Consolas,Menlo,monospace',fontSize:'1.13em',margin:'12px 0 10px 0',letterSpacing:0.01}}>G = Q / (C  t)</span><br/>
          <span style={{color:'#4b5a6a'}}>G</span>    (/), <span style={{color:'#4b5a6a'}}>Q</span>    (), <span style={{color:'#4b5a6a'}}>C</span>     (  , ,   1.163 /(C)), <span style={{color:'#4b5a6a'}}>t</span>       (C).<br/>
             (   ):<br/>
          <span style={{display:'inline-block',color:'#232837',background:'#e6f3fa',padding:'7px 15px',borderRadius:7,fontFamily:'Consolas,Menlo,monospace',fontSize:'1.13em',margin:'12px 0 10px 0',letterSpacing:0.01}}>Q = G  C  t</span><br/>
             ,             .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>  </span><br/>
          <b>  .</b>       ,  , ,   ,          .<br/>
          <b>.</b>       ,      .<br/>
          <b>  .</b>      , ,     .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>   </span><br/>
             (, , ,    ).<br/>
               .<br/>
                .<br/>
                .<br/>
               ,    ,      .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}> </span><br/>
          <b> 1.    </b><br/>
          :  15 , t = 20C,   .<br/>
          : G = 15 / (1.163  20)  0.65 /<br/>
          :   15    0,65 / (10,8 /).<br/><br/>
          <b> 2.    </b><br/>
          :   2 /, t = 10C.<br/>
          : Q = 2  1.163  10 = 23,3 <br/>
          :     23,3  .<br/><br/>
          <b> 3.   </b><br/>
          :  12 , t = 15C,    40% (C  1,03).<br/>
          : G = 12 / (1.03  15)  0.78 /<br/>
          :    0,78 /.
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>   (FAQ)</span><br/>
          <b>1.     ?</b><br/>
               1020C,     510C.   t    ,      (,     t = 15C).<br/><br/>
          <b>2.       ?</b><br/>
            ,         ,     .<br/><br/>
          <b>3.     ?</b><br/>
            , , , ,       .<br/><br/>
          <b>4.       ?</b><br/>
          ,         : , ,  ,   .<br/><br/>
          <b>5.      ?</b><br/>
                  .      .
        </div>
      </div>
    </>
  );
};

export default FlowCalculator; 