import React from 'react';

const VentilationResults = ({ 
  power, 
  heaterType, 
  waterFlow, 
  electricityConsumption, 
  operatingCost,
  gasConsumption,
  gasCost,
  annualEnergy,
  electricityCost,
  gasCostAnnual
}) => {
  return (
    <>
      {/*      */}
      <div className="results" style={{padding:'18px 0 8px 0'}}>
        {annualEnergy !== undefined && annualEnergy !== null && annualEnergy !== '' && (
          <div className="result-item" style={{marginBottom:10, display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
            <span className="result-label"> </span>
            <span className="result-value" style={{fontWeight:600, marginTop:2}}>{annualEnergy} /</span>
          </div>
        )}
        {electricityCost !== undefined && electricityCost !== null && electricityCost !== '' && (
          <div className="result-item" style={{marginBottom:10, display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
            <span className="result-label">  </span>
            <span className="result-value" style={{fontWeight:600, marginTop:2}}>{Math.round(electricityCost)} /</span>
          </div>
        )}
        {gasCostAnnual !== undefined && gasCostAnnual !== null && gasCostAnnual !== '' && (
          <div className="result-item" style={{marginBottom:10, display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
            <span className="result-label">  </span>
            <span className="result-value" style={{fontWeight:600, marginTop:2}}>{gasCostAnnual} /</span>
          </div>
        )}
      </div>

      {/*      */}
      <div className="results" style={{padding:'18px 0 8px 0', display: 'none'}}>
        {heaterType === 'water' && (
          <>
            {gasConsumption && (
              <div className="result-item">
                <span className="result-label"> </span>
                <span className="result-value">{gasConsumption} /</span>
              </div>
            )}
            {gasCost && (
              <div className="result-item">
                <span className="result-label"> </span>
                <span className="result-value">{gasCost} /</span>
              </div>
            )}
          </>
        )}

        {heaterType === 'electric' && electricityConsumption && (
          <>
            <div className="result-item">
              <span className="result-label"> </span>
              <span className="result-value">{electricityConsumption} /</span>
            </div>
            {operatingCost && (
              <div className="result-item">
                <span className="result-label"> </span>
                <span className="result-value">{operatingCost} /</span>
              </div>
            )}
          </>
        )}
      </div>
    </>
  );
};

export default VentilationResults; 