import React, { useState, useEffect } from 'react';
import './FlowCalculator.css';

const FLUIDS = [
  {
    value: 'water',
    label: '',
    getProperties: (temp) => {
      //      0-100C
      const density = 1000 * (1 - (temp - 4) * (temp - 4) / 800000);
      const viscosity = 1.79e-6 * Math.exp(-0.024 * temp);
      return {
        density: parseFloat(density.toFixed(1)),
        viscosity: parseFloat(viscosity.toFixed(8)),
      };
    },
  },
  {
    value: 'glycol30',
    label: '30% ',
    getProperties: (temp) => {
      const density = 1058 - 0.3 * temp;
      const viscosity = 4.5e-6 * Math.exp(-0.02 * temp);
      return {
        density: parseFloat(density.toFixed(1)),
        viscosity: parseFloat(viscosity.toFixed(8)),
      };
    },
  },
  {
    value: 'glycol50',
    label: '50% ',
    getProperties: (temp) => {
      const density = 1088 - 0.35 * temp;
      const viscosity = 8.0e-6 * Math.exp(-0.025 * temp);
      return {
        density: parseFloat(density.toFixed(1)),
        viscosity: parseFloat(viscosity.toFixed(8)),
      };
    },
  },
];

const PIPE_MATERIALS = [
  { value: 'ppr', label: '', roughness: 0.000007 },
  { value: 'copper', label: '', roughness: 0.0000015 },
  { value: 'stainless', label: ' ', roughness: 0.000045 },
];

const FLOW_UNITS = [
  { value: 'm3h', label: '/', toM3s: (v) => v / 3600 },
  { value: 'lmin', label: '/', toM3s: (v) => v / 60000 },
  { value: 'm3s', label: '/', toM3s: (v) => v },
];

function formatNumber(num, decimals = 4) {
  if (num === '-' || isNaN(num)) return '-';
  if (Math.abs(num) < 0.0001 && num !== 0) {
    return Number(num).toExponential(decimals);
  }
  return Number(num).toLocaleString('ru-RU', {
    minimumFractionDigits: 2,
    maximumFractionDigits: decimals,
  });
}

const HydraulicResistanceCalculator = ({ onBack }) => {
  //  
  const [pipeMaterial, setPipeMaterial] = useState('ppr');
  const [fluid, setFluid] = useState('water');
  const [temperature, setTemperature] = useState(20);
  const [flowRate, setFlowRate] = useState('1');
  const [flowUnit, setFlowUnit] = useState('m3h');
  const [diameter, setDiameter] = useState('20');
  const [length, setLength] = useState('50');
  const [tooltip, setTooltip] = useState(false);
  //        
  const [velocity, setVelocity] = useState('');
  const [isVelocityManual, setIsVelocityManual] = useState(false);

  //  
  const fluidObj = FLUIDS.find(f => f.value === fluid);
  const { density, viscosity } = fluidObj.getProperties(Number(temperature));

  // 
  let results = {
    velocity: '-',
    reynolds: '-',
    friction: '-',
    headloss: '-',
    pressure: '-',
    pressureBar: '-',
  };

  //   
  const Q = FLOW_UNITS.find(u => u.value === flowUnit).toM3s(Number(flowRate));
  const D = Number(diameter) / 1000; //  -> 
  const L = Number(length);
  const rho = density;
  const nu = viscosity;
  const roughness = PIPE_MATERIALS.find(m => m.value === pipeMaterial).roughness;

  let autoVelocity = '-';
  if (
    !isNaN(Q) && Q > 0 &&
    !isNaN(D) && D > 0 &&
    !isNaN(L) && L > 0
  ) {
    const A = Math.PI * D * D / 4;
    const V = Q / A;
    autoVelocity = V;
    const Re = V * D / nu;
    //   
    let lambda;
    if (Re < 2000) {
      lambda = 64 / Re;
    } else {
      // -
      const eps = roughness / D;
      let l0 = 0.02;
      let l1;
      let error = 1;
      let iter = 0;
      while (error > 1e-6 && iter < 100) {
        const sqrtL = Math.sqrt(l0);
        l1 = 1 / Math.pow(-2 * Math.log10(eps / 3.7 + 2.51 / (Re * sqrtL)), 2);
        error = Math.abs(l1 - l0);
        l0 = l1;
        iter++;
      }
      lambda = l0;
    }
    //  
    const hf = lambda * L * V * V / (D * 2 * 9.81);
    //  
    const dP = hf * rho * 9.81;
    results = {
      velocity: V.toLocaleString('ru-RU', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + ' /',
      reynolds: formatNumber(Re, 2),
      friction: formatNumber(lambda, 6),
      headloss: hf.toLocaleString('ru-RU', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + '  ',
      pressure: (dP / 1000).toLocaleString('ru-RU', { minimumFractionDigits: 1, maximumFractionDigits: 1 }) + ' ',
      pressureBar: formatNumber(dP / 100000, 5) + ' ',
    };
  }

  //  velocity  -,    
  useEffect(() => {
    if (!isVelocityManual && autoVelocity !== '-') {
      setVelocity(autoVelocity.toFixed(2));
    }
    // eslint-disable-next-line
  }, [flowRate, diameter, flowUnit, isVelocityManual, autoVelocity]);

  //       
  useEffect(() => {
    if (isVelocityManual && velocity !== '' && !isNaN(Number(velocity)) && Number(velocity) > 0 && !isNaN(D) && D > 0) {
      const A = Math.PI * D * D / 4;
      const newQ = Number(velocity) * A; // /
      //      
      let newFlowRate = newQ;
      if (flowUnit === 'm3h') newFlowRate = newQ * 3600;
      if (flowUnit === 'lmin') newFlowRate = newQ * 60000;
      if (flowUnit === 'm3s') newFlowRate = newQ;
      setFlowRate(newFlowRate.toFixed(3));
    }
    // eslint-disable-next-line
  }, [velocity, isVelocityManual, diameter, flowUnit]);

  //    
  const handleVelocityAuto = () => {
    setIsVelocityManual(false);
    if (autoVelocity !== '-') setVelocity(autoVelocity.toFixed(2));
  };

  return (
    <div className="container">
      <div className="card" style={{position: 'relative'}}>
        {onBack && (
          <button
            className="back-btn"
            onClick={onBack}
            aria-label=""
            style={{
              position: 'absolute',
              top: 12,
              left: 12,
              width: 40,
              height: 40,
              borderRadius: 10,
              background: 'var(--main-blue)',
              color: '#fff',
              border: 'none',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
              cursor: 'pointer',
              zIndex: 20,
              transition: 'background 0.2s, transform 0.2s',
              padding: 0,
            }}
            onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
            onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
            onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
          >
            <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15 4L8 11L15 18" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        )}
        <button
          className="info-btn info-btn-square"
          tabIndex={0}
          aria-label=""
          style={{
            position: 'absolute',
            top: 12,
            right: 12,
            width: 40,
            height: 40,
            borderRadius: 10,
            background: 'var(--main-blue)',
            color: '#fff',
            border: 'none',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            boxShadow: '0 2px 8px rgba(76,201,240,0.13)',
            cursor: 'pointer',
            zIndex: 20,
            transition: 'background 0.2s, transform 0.2s',
            padding: 0,
          }}
          onClick={() => setTooltip(!tooltip)}
          onBlur={() => setTooltip(false)}
          onMouseDown={e => e.currentTarget.style.transform = 'scale(0.93)'}
          onMouseUp={e => e.currentTarget.style.transform = 'scale(1)'}
          onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
        >
          <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="11" cy="11" r="10" stroke="white" strokeWidth="2" fill="none"/>
            <rect x="10" y="7" width="2" height="2" rx="1" fill="white"/>
            <rect x="10" y="11" width="2" height="6" rx="1" fill="white"/>
          </svg>
        </button>
        <div className="header-row">
          <div className="card-title"><br /><br /></div>
        </div>
        <div className="results-block">
          <div className="result-row">
            <span className="result-label">  (V)</span>
            <span className="result-value">{results.velocity}</span>
          </div>
          <div className="result-row">
            <span className="result-label">  (h)</span>
            <span className="result-value">{results.headloss}</span>
          </div>
          <div className="result-row">
            <span className="result-label">  (P)</span>
            <span className="result-value">{results.pressure}</span>
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="velocity"> , /</label>
          <div style={{display:'flex', gap:8, alignItems:'center'}}>
            <input
              type="number"
              id="velocity"
              min="0.01"
              max="5"
              step="0.01"
              value={velocity}
              onChange={e => {
                setIsVelocityManual(true);
                setVelocity(e.target.value);
              }}
              inputMode="decimal"
              style={{flex:2}}
            />
            <button
              type="button"
              aria-label="- "
              title="     - "
              onClick={handleVelocityAuto}
              style={{
                flex: 'none',
                background: !isVelocityManual ? '#2196f3' : '#eaf6ff',
                border: !isVelocityManual ? '2px solid #1976d2' : '1px solid #b3e0ff',
                borderRadius: 6,
                padding: '0 8px',
                height: 32,
                cursor: 'pointer',
                color: !isVelocityManual ? '#fff' : '#2196f3',
                fontWeight: !isVelocityManual ? 600 : 400,
                boxShadow: !isVelocityManual ? '0 0 0 2px #90caf9' : 'none',
                transition: 'all 0.18s',
                outline: !isVelocityManual ? '2px solid #1976d2' : 'none',
              }}
              disabled={!isVelocityManual && velocity === autoVelocity.toFixed(2)}
            >
              
            </button>
          </div>
          <div className="slider-block">
            <input
              type="range"
              id="velocity-range"
              min="0.01"
              max="5"
              step="0.01"
              value={velocity}
              onChange={e => {
                setIsVelocityManual(true);
                setVelocity(e.target.value);
              }}
              style={{width:'100%'}}
            />
          </div>
        </div>
        <div className="form-group">
          <label> </label>
          <div className="units-row">
            <input
              type="number"
              id="flow-rate"
              step="0.001"
              min="0"
              placeholder=" "
              value={flowRate}
              onChange={e => setFlowRate(e.target.value)}
              inputMode="numeric"
              style={{flex: 2}}
            />
            <select
              id="flow-unit"
              value={flowUnit}
              onChange={e => setFlowUnit(e.target.value)}
              style={{flex: 1, maxWidth: 120}}
            >
              {FLOW_UNITS.map(u => (
                <option key={u.value} value={u.value}>{u.label}</option>
              ))}
            </select>
          </div>
          <div className="slider-block">
            <input
              type="range"
              id="flow-rate-range"
              min={flowUnit === 'm3h' ? 0 : flowUnit === 'lmin' ? 0 : 0}
              max={flowUnit === 'm3h' ? 10 : flowUnit === 'lmin' ? 200 : 1}
              step={flowUnit === 'm3h' ? 0.01 : flowUnit === 'lmin' ? 1 : 0.001}
              value={flowRate}
              onChange={e => setFlowRate(e.target.value)}
              style={{width: '100%'}}
            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="length"> , </label>
          <input
            type="number"
            id="length"
            step="0.1"
            min="0"
            placeholder=" "
            value={length}
            onChange={e => setLength(e.target.value)}
            inputMode="numeric"
          />
          <div className="slider-block">
            <input
              type="range"
              id="length-range"
              min="0.1"
              max="100"
              step="0.1"
              value={length}
              onChange={e => setLength(e.target.value)}
            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="diameter">   ()</label>
          <input
            type="number"
            id="diameter"
            min="10"
            max="50"
            step="1"
            value={diameter}
            onChange={e => setDiameter(e.target.value)}
            inputMode="numeric"
          />
          <div className="slider-block">
            <input
              type="range"
              id="diameter-range"
              min="10"
              max="50"
              step="1"
              value={diameter}
              onChange={e => setDiameter(e.target.value)}
            />
          </div>
        </div>
        <div className="form-group" style={{marginBottom: 18}}>
          <label htmlFor="pipe-material"> </label>
          <select id="pipe-material" value={pipeMaterial} onChange={e => setPipeMaterial(e.target.value)}>
            {PIPE_MATERIALS.map(m => (
              <option key={m.value} value={m.value}>{m.label}</option>
            ))}
          </select>
        </div>
        <div className="form-group">
          <label htmlFor="fluid"></label>
          <select id="fluid" value={fluid} onChange={e => setFluid(e.target.value)}>
            {FLUIDS.map(f => (
              <option key={f.value} value={f.value}>{f.label}</option>
            ))}
          </select>
        </div>
        <div className="form-group">
          <label htmlFor="temperature"> , C</label>
          <input
            type="number"
            id="temperature"
            min="0"
            max="100"
            step="1"
            value={temperature}
            onChange={e => setTemperature(e.target.value)}
            inputMode="numeric"
          />
          <div className="slider-block">
            <input
              type="range"
              id="temperature-range"
              min="0"
              max="100"
              step="1"
              value={temperature}
              onChange={e => setTemperature(e.target.value)}
            />
          </div>
        </div>
        <div className="fluid-properties" style={{background: 'var(--result-bg)', borderRadius: 8, padding: 15, marginBottom: 20, fontSize: 14}}>
          <div className="fluid-property" style={{display: 'flex', justifyContent: 'space-between', marginBottom: 5}}>
            <span className="fluid-property-label" style={{color: '#98a6c7'}}>:</span>
            <span className="fluid-property-value" id="density-display" style={{color: 'var(--input-text)'}}>{density} /</span>
          </div>
          <div className="fluid-property" style={{display: 'flex', justifyContent: 'space-between'}}>
            <span className="fluid-property-label" style={{color: '#98a6c7'}}> :</span>
            <span className="fluid-property-value" id="viscosity-display" style={{color: 'var(--input-text)'}}>{Number(viscosity).toExponential(3)} /</span>
          </div>
        </div>
        <div className={`tooltip${tooltip ? ' active' : ''}`}>
          <strong> :</strong>
          <span className="formula"> = 64/Re ()  -{`\n`}h = LV/(D2g){`\n`}P = hg</span>
          <b></b>   <br />
          <b>V</b>   , /<br />
          <b>Re</b>   <br />
          <b>D</b>  , <br />
          <b>L</b>  , <br />
          <b></b>  , /<br />
          <b>g</b>    , 9.81 /<br />
          <br />
          <b>:</b>
          <ul style={{margin:0, paddingLeft:20}}>
            <li>         </li>
            <li>   -   </li>
            <li>: P    , h  </li>
          </ul>
        </div>
        <div className="footer-signature">Galf Design</div>
      </div>
      <div className="footer-signature" style={{
        marginTop: 32,
        background: '#f6f7fa',
        borderRadius: 14,
        padding: '22px 16px 20px 16px',
        color: '#232837',
        fontSize: '1.08em',
        lineHeight: 1.85,
        boxShadow: '0 2px 12px 0 rgba(76,201,240,0.07)',
        border: '1.5px solid #e0e4ea',
        maxWidth: 600,
        marginLeft: 'auto',
        marginRight: 'auto',
        position: 'relative',
        textAlign: 'left',
        letterSpacing: 0.01
      }}>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}> ?</span><br/>
                         , , ,    .        , , ,     .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}> ?</span><br/>
           ,    ,     .    ,   ()    (, ).      ,      .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:0}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}> </span><br/>
          <span style={{display:'inline-block',color:'#232837',background:'#e6f3fa',padding:'7px 15px',borderRadius:7,fontFamily:'Consolas,Menlo,monospace',fontSize:'1.13em',margin:'12px 0 10px 0',letterSpacing:0.01}}> = 64/Re ()  -<br/>h = LV/(D2g)<br/>P = hg</span><br/>
          <span style={{color:'#4b5a6a'}}></span>   , <span style={{color:'#4b5a6a'}}>V</span>  , <span style={{color:'#4b5a6a'}}>Re</span>   , <span style={{color:'#4b5a6a'}}>D</span>  , <span style={{color:'#4b5a6a'}}>L</span>  , <span style={{color:'#4b5a6a'}}></span>  , <span style={{color:'#4b5a6a'}}>g</span>    .    30.13330   .
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>  </span><br/>
          <div style={{background:'#f0f4fa', color:'#232837', borderRadius:8, padding:'13px 15px', margin:'12px 0 0 0', fontSize:'0.99em', lineHeight:1.7, boxShadow:'0 1px 6px #4cc9f00a'}}>
            <b>:</b>         <b>25 </b>   <b>40 </b>   <b>1.2 /</b> .<br/><br/>
            <b>:</b>   25 ,  40 ,  1.2 /,  ""  "".   ,     .           .<br/>
          </div>
        </div>
        <div style={{margin:'18px 0 18px 0',borderTop:'1px solid #e0e4ea'}}></div>
        <div style={{marginBottom:18}}>
          <span style={{color:'#4b5a6a',fontWeight:700, fontSize:'1.07em'}}>  </span><br/>
               ,   .  <b></b>          .        .
        </div>
      </div>
    </div>
  );
};

export default HydraulicResistanceCalculator; 